<template>
  <span>
    <b-card no-body>
      <b-card-header>
        <strong>{{ opportunity.node.title }}</strong>
        &nbsp;<b-badge variant="danger" v-if="opportunity.node.rnuv == 0">DEAD</b-badge>
      </b-card-header>
      <div class="row">
        <div class="col-md-3">
          <a :href="opportunity.node.redditUri" target="_blank"><b-img-lazy fluid :src="opportunity.node.memeUri" /></a>
        </div>
        <div class="col-md-6">
          <!-- <div ref="chart"></div> -->
          <apexchart type="line" :options="chart" :series="series"></apexchart>
        </div>
        <div class="col-md-3">
          <strong>Total Invested:</strong> {{ opportunity.node.invested }}<br />
          <strong>Total Upvotes:</strong> {{ opportunity.node.upvoted }}<br />
          <strong>Net Asset Value:</strong> {{ opportunity.node.nuv }}<br />
          <strong>Recent Net Asset Value:</strong> {{ opportunity.node.rnuv }}<br />
        </div>
      </div>
    </b-card>
  </span>
</template>

<script>
export default {
  data () {
    return {
      chart: {},
      series: []
    }
  },
  props: {
    opportunity: {
      type: Object,
      default() {
        return {
          node: {
            investments: []
          }
        }
      }
    }
  },
  beforeUpdate () {
    this.plotData();
  },
  created () {
    this.chart = {
      chart: {
        type: 'line'
      },
      legend: {
        horizontalAlign: 'right',
      },
      series: [{
        name: 'updoots',
        data: []
      },
      {
        name: 'investments',
        data: []
      }],
      yaxis: [{
        title: { text: 'Updoots' }
      },
      {
        title: { text: 'Investments' },
        seriesName: 'investments',
        opposite: true
      }],
      xaxis: {
        type: 'datetime'
      },
      theme: {
        palette: "palette2"
      }
    };

    this.plotData();
  },
  methods: {
    plotData() {
      let prevAmount = 0;

      this.series = [{
        name: 'updoots',
        data: this.opportunity.node.investments
          .map(e => [
            new Date(e.timestamp).getTime(),
            e.upvotes
          ])
      },
      {
        name: 'investments',
        data: this.opportunity.node.investments
          .map(e => {
            prevAmount = prevAmount + e.amount;
            
            return [
              new Date(e.timestamp).getTime(),
              prevAmount
            ];
          })
      }];
    }
  }
}
</script>

<style scoped>
  .card {
    margin-bottom: 1rem;
  }
</style>
